.pagination {
    text-align: center;
    margin-top: 25px;
}
.pagination button {
    float: none;
    margin: 0px 20px;
}
.pagination span{
    padding: 5px;
    margin: 0 1px;
    min-width: 32px;
    height: 32px;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    text-align: center;
    vertical-align: top;
    display: inline-block;
    float: none;
}
.selected {
    font-weight: 800;
    border: 1px solid #000;
    border-radius: 50%;
}