
.users{
    display: grid;
    grid-template-columns: 6fr 6fr;
}
.userItem {
    display: grid;
    grid-template-columns: 3fr 9fr;
    margin: 10px 10px;
    border: 1px solid #fff;
    border-radius: 15px;
    padding: 20px 0px;
}
.userAva {
    text-align: center;
    margin: 0px  20px;
}
.userAva img {
    width: 100px;
    border-radius: 50%;
    padding: 10px;
    background-color: white;
}
.userAva button {
    margin-top: 10px;
    float: none;
    background: #f5f5f5;
    color: #000;
    border: 1px solid;
}
.userInfo {
    display: grid;
    grid-template-columns: 6fr 6fr;
}
.userInfo .userName {

}
.userInfo .userName p{
    font-weight: 700;


}
.userInfo .userLocation {

}
.pagination {
    text-align: center;
}
.pagination span{
    padding: 5px;
    margin: 0 1px;
    min-width: 32px;
    height: 32px;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    text-align: center;
    vertical-align: top;
    display: inline-block;
    float: none;
}
.selected {
    font-weight: 800;
    border: 1px solid #000;
    border-radius: 50%;
}